.collection-wrapper{
    background-color: var(--inactive-color);
}

.collection{
    padding: 15px 0px 40px;
}

.collection-subtitle-row{
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.collection-subtitle-text{
    display: flex;
}

.collection-right{
    color: var(--zomato-red);
}

.collection-cover{
    height: 320px;
    width: 260px;
    margin-right: 20px;
    border-radius: 6px;
    position: relative;
}

.gradient-bg{
    position: absolute;
    height: 100%;
    width: 100%;
    background: var(--bg-gradient); 
    top: 0;
    left: 0;
    border-radius: 6px;
}

.collection-image{
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 6px;
}

.collection-card-title{
    position: absolute;
    left: 16px;
    bottom: 30px;
    font-size: 18px;
    color: var(--primary-bg);
}

.collection-card-subtitle{
    display: flex;
    color: var(--primary-bg);
    position: absolute;
    left: 16px;
    bottom: 10px;
    font-size: 14px;
}